async function generarPDFdatosAlumnos(usuarios) {
    if (!usuarios || !Array.isArray(usuarios)) {
        console.error("No se recibi un array vlido de usuarios.");
        return;
    }
	const centroEducativo = localStorage.getItem("centroeducativoProfe");
	const idClase = localStorage.getItem("idClaseProfe");
    const clases = {
        "4a": "4 ESO | Grupo A",
		"4b": "4 ESO | Grupo B",
		"4c": "4 ESO | Grupo C",
		"4d": "4 ESO | Grupo D",
		"4e": "4 ESO | Grupo E",
		"1BachA": "1 Bachillerato | Grupo A",
		"1BachB": "1 Bachillerato | Grupo B",
		"1BachC": "1 Bachillerato | Grupo C",
		"1BachD": "1 Bachillerato | Grupo D",
		"1BachE": "1 Bachillerato | Grupo E",
		"2BachA": "2 Bachillerato | Grupo A",
		"2BachB": "2 Bachillerato | Grupo B",
		"2BachC": "2 Bachillerato | Grupo C",
		"2BachD": "2 Bachillerato | Grupo D",
		"2BachE": "2 Bachillerato | Grupo E"
    };
	
    const Clasedocente = clases[idClase] || "Clase no definida";

    if (window.jspdf && window.jspdf.jsPDF) {
        const { jsPDF } = window.jspdf;
        const doc = new jsPDF();

        const fecha = new Date();
        const fechaActual = fecha.getDate() + "-" + (fecha.getMonth() + 1) + "-" + fecha.getFullYear();
		
		const imagenLogo64 = '';
		doc.addImage(imagenLogo64, 'PNG', 0, 0, 70, 55);

        doc.setFontSize(18);
        doc.text('Reporte de Alumnos', 105, 20, null, null, 'center');
        doc.setFontSize(11);
        doc.text(`${fechaActual}`, 170, 15);
		doc.setFont('helvetica', 'bold'); 
		doc.text(`Centro Educativo:`, 10, 40);
		doc.setFont('helvetica', 'normal'); 
		doc.text(`${centroEducativo}`, 45, 40);
		doc.setFont('helvetica', 'bold');
        doc.text(`Clase:`, 10, 50);
		doc.setFont('helvetica', 'normal');
		doc.text(`${Clasedocente}`, 23, 50);

        let y = 70;
        const stepY = 10;

        doc.setFontSize(10);
		doc.setFont('helvetica', 'bold');
		doc.text("Alumno", 10, y);
		doc.text("Tiempo", 60, y - 4);
		doc.text("Juego", 60, y);
		doc.text("Progreso", 75, y);
		doc.text("Rango", 93, y);
		doc.text("Preguntas", 128, y - 4);
		doc.text("Acertadas", 128, y);
		doc.text("Preguntas", 147, y - 4);
		doc.text("Falladas", 147, y);
		doc.text("Nivel", 165, y - 4);
		doc.text("Actual", 165, y);
		doc.text("Racha", 177, y - 4);
		doc.text("Superv.", 177, y);
		doc.text("Tiempo", 191, y - 4);
		doc.text("Didct.", 191, y);
		y += stepY;

        usuarios.forEach(alumno => {
			doc.setFont('helvetica', 'normal');
			doc.text(`${alumno.nombre} ${alumno.apellido}`, 10, y);
			doc.text(alumno.minutos.toString(), 60, y);
			doc.text(alumno.progreso.toString() + "%", 75, y);
			doc.text(alumno.rango, 93, y);
			doc.text(alumno.preguntasAcertadas.toString(), 128, y);
			doc.text(alumno.preguntasFalladas.toString(), 147, y);
			doc.text(alumno.nivelActual.toString(), 165, y);
			doc.text(alumno.rachaSupervivencia.toString(), 177, y);
			doc.text(alumno.tiempoDidactico.toString(), 191, y );
			
			y += stepY;

            if (y > 280) {
                doc.addPage();
                y = 20;
            }
        });

        const blob = doc.output("blob");
        previsualizarPDFdatosAlumnos(blob);

		const nombreClaseValido = Clasedocente.replace(/[^a-zA-Z0-9 -]/g, '');
		doc.save(`Reporte-Alumnos-${nombreClaseValido}_${fechaActual}.pdf`);
    } else {
        console.error('jsPDF no est disponible.');
    }
}


