async function mostrarTodosLosAlumnosYCalcularMediaRango() {
    const idcentroDelProfesor = parseInt(localStorage.getItem("idCentroProfe"));
	const idclaseProfesor = localStorage.getItem("idClaseProfe");
    if (!idcentroDelProfesor || !idclaseProfesor) {
        console.error("No se encontr el centro educativo o la clase del profesor");
        return;
    }

    const usuariosRef = collection(db, "usuarios");
    const qUsuarios = query(usuariosRef,
		where("idCentro", "==", idcentroDelProfesor),
		where("idClase", "==", idclaseProfesor),
		orderBy("apellido"));

    try {
        const usuariosSnapshot = await getDocs(qUsuarios);
        let sumaRangos = 0;
        let contadorUsuarios = 0;

        let promesasUsuarios = usuariosSnapshot.docs.map(async (docUsuario) => {
            const usuario = docUsuario.data();
            const usuarioId = docUsuario.id;
            const datosUsuarioRef = doc(db, "datosusuario", usuarioId);
            const datosUsuarioSnap = await getDoc(datosUsuarioRef);

            if (datosUsuarioSnap.exists()) {
                const datosUsuario = datosUsuarioSnap.data();
                const nivelActual = datosUsuario.nivelactual || 0;
                const preguntasAcertadas = datosUsuario.preguntasacertadas || 0;
                const preguntasTotales = (datosUsuario.preguntasacertadas || 0) + (datosUsuario.preguntasfalladas || 0);
                const tiempoDiapositivas = datosUsuario.tiempodidactico || 0;
                
                const rango = 70 * (nivelActual / 15) +
                              20 * ((preguntasTotales > 0 ? preguntasAcertadas / preguntasTotales : 0)) +
                              10 * (tiempoDiapositivas / 500);
                
                sumaRangos += rango;
                contadorUsuarios++;
            }
        });

        await Promise.all(promesasUsuarios);

        const mediaRango = contadorUsuarios > 0 ? Math.round(sumaRangos / contadorUsuarios) : 0;
        const lang = localStorage.getItem('selectedLanguage') || 'es';
        const translations = await $.getJSON(`/translation/${lang}.json`);
        const textoMediaRango = getSynthesizerRangeTranslation(mediaRango, translations);
        
        window.textoMediaRango = textoMediaRango;
		
		const mediaRangoDiv = document.getElementById("Rangopromedioalumnos");
        mediaRangoDiv.innerHTML = `<div>(${mediaRango})</div>`;
		
		const textoMediaRangoDiv = document.getElementById("TextoSintetizadorpromedioalumnos");
        textoMediaRangoDiv.innerHTML = `<div>${textoMediaRango}</div>`;
		
		const progressBarGrupal = document.getElementById('progressBarGrupal');
		progressBarGrupal.style.width = mediaRango + '%';
		progressBarGrupal.setAttribute('aria-valuenow', mediaRango);
		
        const metricasGrupales = await mostrarMediaGrupalTodosLosAlumnos();
		
        return { mediaRango, textoMediaRango, ...metricasGrupales };
    } catch (error) {
        console.error("Error al obtener los alumnos:", error);
        return { error };
    }
}


async function calcularValorGrupal(db, categoriaBien, categoriaMal) {
    const idcentroDelProfesor = parseInt(localStorage.getItem("idCentroProfe"));
	const idclaseProfesor = localStorage.getItem("idClaseProfe");
    if (!idcentroDelProfesor || !idclaseProfesor) {
        console.error("No se encontr el centro educativo o la clase del profesor");
        return;
    }

    const usuariosRef = collection(db, "usuarios");
    const qUsuarios = query(usuariosRef,
		where("idCentro", "==", idcentroDelProfesor),
		where("idClase", "==", idclaseProfesor));
    let sumaBien = 0;
    let sumaMal = 0;

    try {
        const usuariosSnapshot = await getDocs(qUsuarios);
        
        const promesasUsuarios = usuariosSnapshot.docs.map(async (docUsuario) => {
            const usuarioId = docUsuario.id;
            const datosUsuarioRef = doc(db, "datosusuario", usuarioId);
            const datosUsuarioSnap = await getDoc(datosUsuarioRef);

            if (datosUsuarioSnap.exists()) {
                const datosUsuario = datosUsuarioSnap.data();
                sumaBien += datosUsuario[categoriaBien] || 0;
                sumaMal += datosUsuario[categoriaMal] || 0;
            } else {
                console.log(`No se encontraron datos para el usuario ID: ${usuarioId}`);
            }
        });

        await Promise.all(promesasUsuarios);

        const porcentajeAcierto = sumaBien + sumaMal > 0 ? Math.round(sumaBien / (sumaBien + sumaMal) * 100) : 0;
        return porcentajeAcierto;
    } catch (error) {
        console.error(`Error al calcular porcentaje de acierto para ${categoriaBien}:`, error);
        return 0;
    }
}



