async function mostrarMediaGrupalTodosLosAlumnos() {
    const idcentroDelProfesor = parseInt(localStorage.getItem("idCentroProfe"));
	const idclaseProfesor = localStorage.getItem("idClaseProfe");
    if (!idcentroDelProfesor || !idclaseProfesor) {
        console.error("No se encontr el centro educativo o la clase del profesor");
        return;
    }

    const usuariosRef = collection(db, "usuarios");
    const qUsuarios = query(usuariosRef,
		where("idCentro", "==", idcentroDelProfesor),
		where("idClase", "==", idclaseProfesor),
		orderBy("apellido"));

    try {
        const usuariosSnapshot = await getDocs(qUsuarios);
        let sumaMinutos = 0, sumaProgreso = 0, sumaPreguntasAcertadas = 0, sumaPreguntasFalladas = 0, sumaNivelActual = 0, sumaRachaSupervivencia = 0, sumaTiempoDidactico = 0;
        let totalUsuarios = usuariosSnapshot.docs.length;

        let promesasUsuarios = usuariosSnapshot.docs.map(async (docUsuario) => {
            const usuario = docUsuario.data();
            const usuarioId = docUsuario.id;
            const datosUsuarioRef = doc(db, "datosusuario", usuarioId);
            const datosUsuarioSnap = await getDoc(datosUsuarioRef);

            if (datosUsuarioSnap.exists()) {
                const datosUsuario = datosUsuarioSnap.data();
                sumaMinutos += datosUsuario.tiempodejuego ? Math.round(datosUsuario.tiempodejuego / 60) : 0;
                sumaProgreso += datosUsuario.nivelactual ? Math.round((datosUsuario.nivelactual / 15) * 100) : 0;
                sumaPreguntasAcertadas += datosUsuario.preguntasacertadas || 0;
                sumaPreguntasFalladas += datosUsuario.preguntasfalladas || 0;
                sumaNivelActual += datosUsuario.nivelactual || 0;
                sumaRachaSupervivencia += datosUsuario.rachasupervivencia || 0;
                sumaTiempoDidactico += datosUsuario.tiempodidactico ? Math.round(datosUsuario.tiempodidactico / 60) : 0;
            }
        });

        await Promise.all(promesasUsuarios);

        // Calcular medias
        const metricasGrupales = {
            mediaMinutos: Math.round(sumaMinutos / totalUsuarios),
            mediaProgreso: Math.round(sumaProgreso / totalUsuarios),
            mediaPreguntasAcertadas: Math.round(sumaPreguntasAcertadas / totalUsuarios),
            mediaPreguntasFalladas: Math.round(sumaPreguntasFalladas / totalUsuarios),
            mediaNivelActual: Math.round(sumaNivelActual / totalUsuarios),
            mediaRachaSupervivencia: Math.round(sumaRachaSupervivencia / totalUsuarios),
            mediaTiempoDidactico: Math.round(sumaTiempoDidactico / totalUsuarios)
        };
		
        return metricasGrupales;
    } catch (error) {
        console.error("Error al obtener los alumnos en grupo:", error);
    }
}


