async function mostrarMediaGrupalMetricasSintesis() {
    const idcentroDelProfesor = parseInt(localStorage.getItem("idCentroProfe"));
	const idclaseProfesor = localStorage.getItem("idClaseProfe");
    if (!idcentroDelProfesor || !idclaseProfesor) {
        console.error("No se encontr el centro educativo o la clase del profesor");
        return;
    }

    const usuariosRef = collection(db, "usuarios");
    const qUsuarios = query(usuariosRef,
		where("idCentro", "==", idcentroDelProfesor),
		where("idClase", "==", idclaseProfesor),
		orderBy("apellido"));

    try {
        const usuariosSnapshot = await getDocs(qUsuarios);
        let totalUsuarios = usuariosSnapshot.docs.length;
        let camposSuma = {
            comodinesreplicacion: 0,
            comodinestranscripcion: 0,
            comodinestraduccion: 0,
            jugadasacertadas: 0,
            jugadasfalladas: 0,
            totaljugadas: 0,
            rachasupervivencia: 0
        };

        let promesasUsuarios = usuariosSnapshot.docs.map(async (docUsuario) => {
            const usuario = docUsuario.data();
            const usuarioId = docUsuario.id;
            const datosUsuarioRef = doc(db, "datosusuario", usuarioId);
            const datosUsuarioSnap = await getDoc(datosUsuarioRef);

            if (datosUsuarioSnap.exists()) {
                const datosUsuario = datosUsuarioSnap.data();
                Object.keys(camposSuma).forEach(campo => {
                    camposSuma[campo] += datosUsuario[campo] || 0;
                });
            }
        });

        await Promise.all(promesasUsuarios);

        let medias = {};
        Object.keys(camposSuma).forEach(campo => {
            medias[campo] = Math.round(camposSuma[campo] / totalUsuarios);
        });

        actualizarTablaGrupalMetricasSintesis(medias);
    } catch (error) {
        console.error("Error al obtener los alumnos:", error);
    }
}

function actualizarTablaGrupalMetricasSintesis(medias) {
    const tbody = document.querySelector("#grupal-metricas-sintesis tbody");
    tbody.innerHTML = `
        <tr>
            <td style="text-align: center;">${medias.comodinesreplicacion}</td>
            <td style="text-align: center;">${medias.comodinestranscripcion}</td>
            <td style="text-align: center;">${medias.comodinestraduccion}</td>
            <td style="text-align: center;">${medias.jugadasacertadas}</td>
            <td style="text-align: center;">${medias.jugadasfalladas}</td>
            <td style="text-align: center;">${medias.totaljugadas}</td>
            <td style="text-align: center;">${medias.rachasupervivencia}</td>
        </tr>
    `;
}

