async function generarPDFdatosGrupo(metricasGrupales) {
	const centroEducativo = localStorage.getItem("centroeducativoProfe");
	const idClase = localStorage.getItem("idClaseProfe");
    const clases = {
        "4a": "4 ESO | Grupo A",
		"4b": "4 ESO | Grupo B",
		"4c": "4 ESO | Grupo C",
		"4d": "4 ESO | Grupo D",
		"4e": "4 ESO | Grupo E",
		"1BachA": "1 Bachillerato | Grupo A",
		"1BachB": "1 Bachillerato | Grupo B",
		"1BachC": "1 Bachillerato | Grupo C",
		"1BachD": "1 Bachillerato | Grupo D",
		"1BachE": "1 Bachillerato | Grupo E",
		"2BachA": "2 Bachillerato | Grupo A",
		"2BachB": "2 Bachillerato | Grupo B",
		"2BachC": "2 Bachillerato | Grupo C",
		"2BachD": "2 Bachillerato | Grupo D",
		"2BachE": "2 Bachillerato | Grupo E"
    };
	
    const Clasedocente = clases[idClase] || "Clase no definida";

    if (window.jspdf && window.jspdf.jsPDF) {
        const { jsPDF } = window.jspdf;
        const doc = new jsPDF();

        const fecha = new Date();
        const fechaActual = fecha.getDate() + "-" + (fecha.getMonth() + 1) + "-" + fecha.getFullYear();
		
		const imagenLogo64 = '';
		doc.addImage(imagenLogo64, 'PNG', 0, 0, 70, 55);
		
		const imagenFoot64 = '';
		doc.addImage(imagenFoot64, 'PNG', 3, 200, 190, 94);

        doc.setFontSize(18);
        doc.text('Reporte Grupal', 105, 20, null, null, 'center');
        doc.setFontSize(11);
        doc.text(`${fechaActual}`, 170, 15);  // Fecha
		doc.setFont('helvetica', 'bold'); 
		doc.text(`Centro Educativo:`, 10, 40);
		doc.setFont('helvetica', 'normal'); 
		doc.text(`${centroEducativo}`, 45, 40);
		doc.setFont('helvetica', 'bold');
        doc.text(`Clase:`, 10, 50);
		doc.setFont('helvetica', 'normal');
		doc.text(`${Clasedocente}`, 23, 50);

        let y = 70;
        const stepY = 10;

        doc.setFontSize(10);
		doc.setFont('helvetica', 'bold');
		doc.text("Tiempo", 10, y - 4);
		doc.text("Juego", 10, y);
		doc.text("Progreso", 30, y);
		doc.text("Rango Grupal", 52, y);
		doc.text("Preguntas", 88, y - 4);
		doc.text("Acertadas", 88, y);
		doc.text("Preguntas", 110, y - 4);
		doc.text("Falladas", 110, y);
		doc.text("Nivel", 132, y - 4);
		doc.text("Actual", 132, y);
		doc.text("Racha", 148, y - 4);
		doc.text("Supervivencia", 148, y);
		doc.text("Tiempo", 177, y - 4);
		doc.text("Didctico", 177, y);
		y += stepY;

		doc.setFont('helvetica', 'normal');
		doc.text(metricasGrupales.mediaMinutos.toString(), 10, y);
		doc.text(metricasGrupales.mediaProgreso.toString() + "%", 30, y);
		doc.text(window.textoMediaRango, 52, y);
		doc.text(metricasGrupales.mediaPreguntasAcertadas.toString(), 88, y);
		doc.text(metricasGrupales.mediaPreguntasFalladas.toString(), 110, y);
		doc.text(metricasGrupales.mediaNivelActual.toString(), 132, y);
		doc.text(metricasGrupales.mediaRachaSupervivencia.toString(), 148, y);
		doc.text(metricasGrupales.mediaTiempoDidactico.toString(), 177, y );

        const blob = doc.output("blob");
        previsualizarPDFdatosGrupo(blob);

		const nombreClaseValidoGrupo = Clasedocente.replace(/[^a-zA-Z0-9 -]/g, '');
		doc.save(`Reporte-Grupal-${nombreClaseValidoGrupo}_${fechaActual}.pdf`);
    } else {
        console.error('jsPDF no est disponible.');
    }
}


