async function mostrarMetricasMinijuegosAlumno(nombreAlumno, apellidoAlumno) {
    const idcentroDelProfesor = parseInt(localStorage.getItem("idCentroProfe"));
	const idclaseProfesor = localStorage.getItem("idClaseProfe");
    if (!idcentroDelProfesor || !idclaseProfesor) {
        console.error("No se encontr el centro educativo o la clase del profesor");
        return;
    }

    if (!nombreAlumno || !apellidoAlumno) {
        console.error("No se encontraron los datos del alumno");
        return;
    }

    const usuariosRef = collection(db, "usuarios");
    const qUsuario = query(usuariosRef,
		where("idCentro", "==", idcentroDelProfesor),
		where("idClase", "==", idclaseProfesor),
		where("nombre", "==", nombreAlumno),
		where("apellido", "==", apellidoAlumno));

    try {
        const usuariosSnapshot = await getDocs(qUsuario);
        if (usuariosSnapshot.empty) {
            console.log('No se encontr el alumno especificado');
            return;
        }

        const docUsuario = usuariosSnapshot.docs[0];
        const usuarioId = docUsuario.id;

        const datosUsuarioRef = doc(db, "datosusuario", usuarioId);
        const datosUsuarioSnap = await getDoc(datosUsuarioRef);

        if (!datosUsuarioSnap.exists()) {
            console.log('No se encontraron datos del usuario especificado en datosusuario');
            return;
        }

        const datosUsuario = datosUsuarioSnap.data();

        actualizarTablaEspecificaMetricasMinijuegos(datosUsuario);
    } catch (error) {
        console.error("Error al obtener los datos del alumno:", error);
    }
}

async function actualizarTablaEspecificaMetricasMinijuegos(datosUsuario) {
    const lang = localStorage.getItem('selectedLanguage') || 'es';
    const translations = await $.getJSON(`/translation/${lang}.json`);

    let minijuegoSupervivencia = datosUsuario.rachasupervivencia;
    let estiloSupervivencia = "color: red;";
    if (minijuegoSupervivencia >= 200) {
        minijuegoSupervivencia = translations['On-a-streak'] || "En Racha! +200";
        estiloSupervivencia = "color: green; font-weight: bold;";
    } else {
        minijuegoSupervivencia = translations['Keep-trying'] || "Sigue intentando";
    }

    let minijuegoMendel = datosUsuario.minigameMendel;
    let estiloMendel = minijuegoMendel ? "color: green; font-weight: bold;" : "color: red;";
    minijuegoMendel = minijuegoMendel ? (translations['Completed'] || "Completado") : (translations['Not-completed'] || "No Completado");

    let minijuegoSanguineo = datosUsuario.minigameSangre;
    let estiloSanguineo = minijuegoSanguineo ? "color: green; font-weight: bold;" : "color: red;";
    minijuegoSanguineo = minijuegoSanguineo ? (translations['Completed'] || "Completado") : (translations['Not-completed'] || "No Completado");

    let minijuegoCromosomas = datosUsuario.minigameHerenciaHumana;
    let estiloCromosomas = minijuegoCromosomas ? "color: green; font-weight: bold;" : "color: red;";
    minijuegoCromosomas = minijuegoCromosomas ? (translations['Completed'] || "Completado") : (translations['Not-completed'] || "No Completado");

    const minijuegoEvolucion = translations['Coming-soon'] || "Prximamente";
    const estiloEvolucion = "color: grey;";

    const minijuegoCelular = translations['Coming-soon'] || "Prximamente";
    const estiloCelular = "color: grey;";

    const tbody = document.querySelector("#individual-metricas-minijuegos tbody");
    tbody.innerHTML = `
        <tr>
            <td style="text-align: center; ${estiloSupervivencia}">${minijuegoSupervivencia}</td>
            <td style="text-align: center; ${estiloMendel}">${minijuegoMendel}</td>
            <td style="text-align: center; ${estiloSanguineo}">${minijuegoSanguineo}</td>
            <td style="text-align: center; ${estiloCromosomas}">${minijuegoCromosomas}</td>
            <td style="text-align: center; ${estiloEvolucion}">${minijuegoEvolucion}</td>
            <td style="text-align: center; ${estiloCelular}">${minijuegoCelular}</td>
        </tr>
    `;
}


function iniciarSesion(email, licencia, rememberme) {

    const docRef = doc(db, "profesores", licencia);

    getDoc(docRef)
        .then((docSnap) => {

            if (docSnap.exists()) {

                if (docSnap.data().email === email) {
					localStorage.setItem('rememberme', rememberme ? 'true' : 'false');
                    localStorage.setItem("licenciaProfe", licencia);
                    const nombreProfe = docSnap.data().nombre;
                    localStorage.setItem("nombreProfe", nombreProfe);
                    const centroeducativoProfe = docSnap.data().centro;
                    localStorage.setItem("centroeducativoProfe", centroeducativoProfe);
                    const emailProfe = docSnap.data().email;
                    localStorage.setItem("emailProfe", emailProfe);
					const idCentroProfe = docSnap.data().idCentro;
					localStorage.setItem("idCentroProfe", idCentroProfe);
					const idClaseProfe = docSnap.data().idClase;
					localStorage.setItem("idClaseProfe", idClaseProfe);
					const fechaExpira = docSnap.data().fechaexpiracion;
					if (fechaExpira && fechaExpira.seconds) {
						localStorage.setItem("fechaExpiraSeconds", fechaExpira.seconds.toString());
					}
                    const tokenUsuario = "token-" + docSnap.id;
                    localStorage.setItem("tokenUsuario", tokenUsuario);
                    window.location.href = 'dashui/dist/index.html';
                    contarAlumnos();
                    mostrarRanking();
                } else {
                    console.error("Error: Email incorrecto");
                    document.getElementById("mensaje-error").textContent = "Email o Licencia incorrecta";
                    document.getElementById("mensaje-error").style.display = 'block';
                }
            } else {
                console.error("Error: Licencia incorrecta");
                document.getElementById("mensaje-error").textContent = "Email o Licencia incorrecta";
                document.getElementById("mensaje-error").style.display = 'block';
            }
        })
        .catch((error) => {
            console.error("Error al buscar en Firestore:", error);
            document.getElementById("mensaje-error").textContent = "Error al iniciar sesin: " + error.message;
            document.getElementById("mensaje-error").style.display = 'block';
        });
}



