function getSynthesizerRangeTranslation(mediaRango, translations) {
    let key;
    if (mediaRango >= 0 && mediaRango <= 20) {
        key = 'Iron-synthesizer';
    } else if (mediaRango >= 21 && mediaRango <= 40) {
        key = 'Bronze-synthesizer';
    } else if (mediaRango >= 41 && mediaRango <= 60) {
        key = 'Silver-synthesizer';
    } else if (mediaRango >= 61 && mediaRango <= 80) {
        key = 'Gold-synthesizer';
    } else if (mediaRango >= 81) {
        key = 'Platinum-synthesizer';
    } else {
        key = 'Unknown-range';
    }
    return translations[key] || key;
}


function setupSwitchesAndLabels(data, translations) {
	if (window.location.pathname.includes('profile-followers.html')) {
		const lockedText = translations.locked || 'BLOQUEADO';
		const unlockedText = translations.unlocked || 'DESBLOQUEADO';

		const switchSupervivencia = document.getElementById('flexSwitchCheckDefaultSupervivencia');
		const labelSupervivencia = document.getElementById('labelSupervivencia');
		switchSupervivencia.checked = data.supervivenciaSeccion;
		labelSupervivencia.textContent = data.supervivenciaSeccion ? unlockedText : lockedText;

		const switchMendel = document.getElementById('flexSwitchCheckDefaultMendel');
		const labelMendel = document.getElementById('labelMendel');
		switchMendel.checked = data.mendelSeccion;
		labelMendel.textContent = data.mendelSeccion ? unlockedText : lockedText;

		const switchSanguineo = document.getElementById('flexSwitchCheckDefaultSanguineo');
		const labelSanguineo = document.getElementById('labelSanguineo');
		switchSanguineo.checked = data.gruposSanguineosSeccion;
		labelSanguineo.textContent = data.gruposSanguineosSeccion ? unlockedText : lockedText;

		const switchCromosomas = document.getElementById('flexSwitchCheckDefaultCromosomas');
		const labelCromosomas = document.getElementById('labelCromosomas');
		switchCromosomas.checked = data.herenciaHumanaSeccion;
		labelCromosomas.textContent = data.herenciaHumanaSeccion ? unlockedText : lockedText;
	}
}


function addSwitchEventListeners(translations) {
    const lockedText = translations.locked || 'BLOQUEADO';
    const unlockedText = translations.unlocked || 'DESBLOQUEADO';

    const switchSupervivencia = document.getElementById('flexSwitchCheckDefaultSupervivencia');
    const labelSupervivencia = document.getElementById('labelSupervivencia');
    if (switchSupervivencia && labelSupervivencia) {
        switchSupervivencia.addEventListener('change', function() {
            labelSupervivencia.textContent = this.checked ? unlockedText : lockedText;
        });
    }

    const switchMendel = document.getElementById('flexSwitchCheckDefaultMendel');
    const labelMendel = document.getElementById('labelMendel');
    if (switchMendel && labelMendel) {
        switchMendel.addEventListener('change', function() {
            labelMendel.textContent = this.checked ? unlockedText : lockedText;
        });
    }

    const switchSanguineo = document.getElementById('flexSwitchCheckDefaultSanguineo');
    const labelSanguineo = document.getElementById('labelSanguineo');
    if (switchSanguineo && labelSanguineo) {
        switchSanguineo.addEventListener('change', function() {
            labelSanguineo.textContent = this.checked ? unlockedText : lockedText;
        });
    }

    const switchCromosomas = document.getElementById('flexSwitchCheckDefaultCromosomas');
    const labelCromosomas = document.getElementById('labelCromosomas');
    if (switchCromosomas && labelCromosomas) {
        switchCromosomas.addEventListener('change', function() {
            labelCromosomas.textContent = this.checked ? unlockedText : lockedText;
        });
    }
}

document.addEventListener('DOMContentLoaded', function() {
    if ($('.dropdown-content-panel').length) {
        const centroeducativoProfe = localStorage.getItem("centroeducativoProfe");
        const centroeducativoProfeFormat = centroeducativoProfe.replace(/\s+/g, '-');
        const idCentro = parseInt(localStorage.getItem("idCentroProfe"));
        const idClase = localStorage.getItem("idClaseProfe");
        const coleccionTraduccionesPanel = `${centroeducativoProfeFormat}-${idCentro}-${idClase}`;

        console.log("Document reference: ", coleccionTraduccionesPanel);
        
        const docRef = doc(db, "controlAccesoClase", coleccionTraduccionesPanel);
        getDoc(docRef)
            .then((docSnap) => {
                if (docSnap.exists()) {
                    const data = docSnap.data();
                    const lenguajePanel = data.lenguajePanel || 'es';
                    updateCurrentLanguageDisplayPanel(lenguajePanel);
                    changePanelLanguage(lenguajePanel);
                    localStorage.setItem('selectedLanguage', lenguajePanel);
                } else {
                    console.log("No existe el documento!");
                }
            })
            .catch((error) => {
                console.log("Error al obtener el documento:", error);
            });

        $('.dropdown-content-panel div').on('click', function() {
            var selectedLanguage = $(this).data('lang');
            updateFirestoreTraduccionesPanel('lenguajePanel', selectedLanguage);
            updateCurrentLanguageDisplayPanel(selectedLanguage);
            changePanelLanguage(selectedLanguage);
            localStorage.setItem('selectedLanguage', selectedLanguage);
        });
    }

    const savedLanguage = localStorage.getItem('selectedLanguage');
    if (savedLanguage) {
        changePanelLanguage(savedLanguage);
        updateCurrentLanguageDisplayPanel(savedLanguage);
    }
});



